<template>
    <div class="login-wrapper">
      <mu-row>
        <mu-col width="10" tablet="20" desktop="40"></mu-col>
        <mu-col width="80" tablet="60" desktop="20">
          <h1 class="login">登录</h1>
          <mu-text-field v-model="email" :fullWidth=true label="邮箱"/><br/>
          <mu-text-field v-model="pass" :fullWidth=true label="密码" type="password"/><br/>
          <mu-flat-button label="登录" @click="login"/>
          <mu-flat-button style="float: right" label="注册" @click="register"/>
          </mu-col>
        <mu-col width="10" tablet="20" desktop="40"></mu-col>
      </mu-row>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      email: '',
      pass: ''
    }
  },
  methods: {
    validEmail () {
      if (!this.email) {
        alert('邮箱不能为空')
        return false
      }
      return true
    },
    validPass () {
      if (!this.pass) {
        alert('密码不能为空')
        return false
      }
      return true
    },
    register () {
      this.$router.push({'name': 'Register'})
    },
    login () {
      var _this = this
      if (_this.validEmail() && _this.validPass()) {
        axios({
          method: 'POST',
          url: '/sessions',
          headers: {
            'Content-Type': 'application/json'
          },
          data: {
            email: _this.email,
            password: _this.pass
          }
        }).then((response) => {
          if (response.data.code === 401.1) {
            alert('密码错误')
          } else if (response.data.code === 401.2) {
            alert('用户未注册')
          } else {
            window.sessionStorage.setItem('key', response.data.key)
            window.sessionStorage.setItem('admin', response.data.admin)
            window.sessionStorage.setItem('avatar', response.data.avatar)
            _this.$router.push({'name': 'HelloWorld'})
          }
        }).catch((error) => {
          console.log(error)
          alert('登录出错')
        })
      }
    }
  }
}
</script>

<style>
  .login-wrapper {
    margin-top: 150px;
  }
</style>



